.hero{
    background-color: rgba(0,0,0,0.5);
    color: #fff;
    height:80vh;
    width:100%;
    position: relative;
    margin-bottom: 4rem;
}
.hero:before{
content: "";
background:url('../../assets/1.jpg') no-repeat center center/cover;
height:100%;
width:100%;
position: absolute;
top: 0;
left: 0;
z-index: -1;
}
.hero .hero-content{
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width:90vw;
    margin:auto;
    padding:1rem;
}
.hero h1{
    font-size:3rem;
    text-transform:capitalize;
}
.hero .search-text{
    margin : 1.5rem;
    font-size:1.5rem;
}
.search{
    display:flex;
    background-color:white;
    color: black;
    justify-content:space-between;
    align-items:center;
    border: 1.3px solid #ccc;
    padding: 0.5rem;
    border-radius:0.6rem;
    
}
.search input[type=text]{
   border:transparent;
   background-color:transparent;
   width: 35vw;
   font-size:1.2rem;
}
.search input[type=text]:focus{
    outline:none;
}
.search label{
    padding:0 0.6rem 0 0.4rem;
}
.search .radio{
    display:flex;
    align-items:center;
}
.search button{
    background-image: linear-gradient(45deg , #5651e5  0% , #709dff 100%);
    border: 1px solid #5651e5;
    color: #fff;
    padding:0.5rem;
    border-radius:0.5rem;
    cursor: pointer;
}

@media screen and (max-width:940px) {
    .hero{
        height:70vh;

    }
    .hero h1{
        font-size: 2.5rem;
    }
    search input[type=text]{
        width:100%;
    }
    
}